<template>
	<div>
		 <ul class="list">
		 	 <li>
		 	 	<div class="textCent">
		 	 		<h2>1.订单详情，查找取车点</h2>
		 	 		<p>在订单详情页查看取车点， 导航到取车车行。</p>
		 	 	</div>
		 	 	<div class="imgCent">
		 	 		<img src="../../assets/up3.png"/>
		 	 	</div>
		 	 </li>
		 	  <li>
		 	 	<div class="textCent">
		 	 		<h2>2.联系商家，找到出租车辆 </h2>
		 	 		<p>展示订单给商家，找到出租车 辆。取车前确认车辆内已经放 入电池哦。</p>
		 	 	</div>
		 	 	<div class="imgCent">
		 	 		<img src="../../assets/up2.png"/>
		 	 	</div>
		 	 </li>
		 	  <li>
		 	 	<div class="textCent">
		 	 		<h2>3.一键扫码，快速取车</h2>
