<script>
  export default {

  }
</script>
<style scoped  lang="scss"  >
	.list li{
		height: 252px;
		width: 100%;
		border-top: 20px solid #F8F8F8;
		padding: 28px 40px;
		box-sizing: border-box;
		.textCent{
			width: 340px;
			float: left;
		}
		.imgCent{
			width: 196px;
			height: 196px;
			float: right;
			 margin-right: 20px;
			img{
				height: 100%;
				width: 100%;
			}

		}
		h2{
			font-size:28px;
			font-family:PingFangSC-Medium;
			font-weight:600;
			color:#333333;
			width: 100%;
			text-align: left;

		}
		p{
			font-size:26px;
			font-family:PingFangSC-Regular;
			font-weight:400;
			color:rgba(153,153,153,1);
			line-height:36px;
			width: 100%;
			margin-top: 20px;
			text-align: left;
		}
	}
	.list li:nth-child(1){
		 border: none;
	}
</style>



